body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.main-page-container {
  display: flex;
  justify-content: center;
}

.main-page-link {
  display: inline-block;
  text-align: center;
  margin-top: 300px;
  color: #000000;
  background: #fff;
  padding: 0.7rem 1.5rem; /* Поля вокруг текста */
  text-decoration: none;
  border: 2px solid #000000;
  border-radius: 4px;
  cursor: pointer;
}

.main-page-name {
  font-size: 20px;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-button {
  width: 100px;
  height: 55px;
  margin-right: 10px;
  cursor: pointer;
  border: 2px solid #000000;
  border-radius: 4px;
}

.button-name {
  font-size: 20px;
}
